import { defineComponent, reactive, ref } from 'vue';
import { rules } from '@/pages/login/config/account-config';
import { useStore } from 'vuex';
import { localCache } from '@/utils/storage';
export default defineComponent({
  name: 'LoginAccount',

  setup() {
    const store = useStore();
    const account = reactive({
      name: '',
      password: ''
    });
    const formRef = ref();

    const loginAction = isKeepPassword => {
      formRef.value?.validate(valid => {
        if (valid) {
          //判断是否记住密码
          if (isKeepPassword) {
            localCache.set('name', account.name);
            localCache.set('password', account.password);
          } else {
            localCache.remove('name');
            localCache.remove('password');
          } //登录验证


          store.dispatch('login/loginAction', { ...account
          });
        }
      });
    };

    return {
      account,
      loginAction,
      formRef,
      rules
    };
  }

});