import { defineComponent, ref } from 'vue';
import LoginAccount from '@/pages/login/componments/LoginAccount.vue';
import LoginPhone from '@/pages/login/componments/LoginPhone.vue';
export default defineComponent({
  name: 'LoginPanel',
  components: {
    LoginPhone,
    LoginAccount
  },

  setup() {
    const isKeepPassword = ref(true);
    const accountRef = ref();
    const phoneRef = ref();
    const currentTab = ref('account');

    const onLogin = () => {
      if (currentTab.value === 'account') {
        accountRef.value?.loginAction(isKeepPassword.value);
      } else {
        console.log('phoneRef调用loginAction');
      }
    };

    return {
      isKeepPassword,
      onLogin,
      accountRef,
      phoneRef,
      currentTab
    };
  }

});